    <crumbs menu='店铺客流' subMenu='客流分析' /> 
    <div class="container sql">
      <el-row type="flex" justify="space-around">
        <el-col :span='4'>
          <market @marketGet='marketGet' :marketSet='marketName'/>
        </el-col>
        <el-col :span='4'>
        <span id="text">选择店铺:</span>
        <el-select v-model="storeName" placeholder="请选择" filterable :style="widthSet">
          <el-option v-for="type in storeType" :label="type.key" :value="type.value" :key="type.value">
          </el-option>
        </el-select>
        </el-col>
        <el-col :span='11'>
          <el-row>
            <el-col :span='3'>
              <span style="line-height:40px;">时间选择:</span>
            </el-col>
             <el-col :span='13'>
                <el-radio-group v-model="time" >
                        <el-radio-button label="近7日"></el-radio-button>
                        <el-radio-button label="近30日"></el-radio-button>
                        <el-radio-button label="近1年"></el-radio-button>
                        <el-radio-button label="自定义时间"></el-radio-button>
                </el-radio-group>
              </el-col>
              <el-col :span='8'>
                <el-date-picker :disabled='datePickerAble' v-model="timeRange" type="daterange" :shortcuts="shortcuts"
                style="width:100%;"
                  range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="right">
                </el-date-picker>
              </el-col>
          </el-row>
        </el-col>
        <el-col :span='5' style="display:flex;justify-content:flex-end;">
            <el-button type="primary" icon="el-icon-search" @click='onQuery'>查询</el-button>
            <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
            <!-- <el-button type="primary" icon="el-icon-document" @click="dialogFormVisible = true">导出</el-button> -->
        </el-col>
      </el-row>
    </div>
    <div>
      <el-card header="店铺客流排行" style="font-weight: bold;" class="marginTop10 marginLeft8">

        <el-row :gutter="20" class="mgb20">
          <el-col :span="8">
            <el-card shadow="hover" :body-style="{ padding: '1px' }">
