                          type: 'category',
                          data: this.timeCalc(this.timeRange[0],this.timeRange[1])
                      }
                  ],
                  yAxis: [
                      {
                          type: 'value'
                      }
                  ],
                  series: data
              };
            myChart.setOption(option);
            return myChart
