                              symbol:"none", 
                              label:{
                                position:"end"          //将警示值放在哪个位置，三个值“start”,"middle","end"  开始  中点 结束
                              },
                          }
                      }]
        this.chart=this.initChart(this.$refs.flow,this.chartData)
      })
    },
    components: {
      crumbs: Crumbs,
      market: MarketSelect,
      indexSelect: IndexSelect,
      timeSelect: TimeSelect,
      chart: Chart
    },
    computed:{
      widthSet(){
        return `width:calc(90% - ${this.textWidth}px);`
      }
    },
    watch:{
      interval(){
        this.intervalChange()
      },
      screenWidth(val){
        // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
        if(!this.timer){
            // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
            this.screenWidth = val
            this.timer = true
            let that = this
            setTimeout(function(){
                // 打印screenWidth变化的值
                console.log(that.screenWidth)
                that.timer = false
                that.chart.resize()
            },400)
        }
    },
    time(val){
      if(val==='近7日'){
        this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
      }
      if(val==="近30日"){
        this.timeRange=[this.$moment().subtract(1, 'months'),this.$moment()]
      }
      if(val==="近1年"){
        this.timeRange=[this.$moment().subtract(1, 'year'),this.$moment()]
      }
      if (val == '自定义时间') {
          this.datePickerAble = false
        } else this.datePickerAble = true
    }
    }
  }
</script>
