<template>
<el-card :header="title" style="font-weight: bold;margin-top:10px" shadow="hover">
  <div ref="myEchart" :style="styleSet" >
  </div>
</el-card>
</template>
<script>
import * as echarts from 'echarts'
export default {
  props:['option','title','height'],
  data(){
    return {
      screenWidth:document.body.clientWidth,
      chart:{}
    }
  },
  mounted(){
    this.init()
    const that = this
      window.onresize = () => {
        return (() => {
            window.screenWidth = document.body.clientWidth
            that.screenWidth = window.screenWidth
        })()
    }
  },
  computed:{
    styleSet(){
      let height=this.height?this.height:'350px'
      return `height:${height};width:100%;`
    }
  },
  methods:{
    init(){
      let _this=this
      this.$nextTick(()=>{
        let myChart=echarts.init(_this.$refs.myEchart);
        myChart.setOption(_this.option);
        _this.chart=myChart
      })
    }
  },
  watch:{
    option() {
      console.log(this.option)
      this.$nextTick(()=>{
        if(this.option){
          this.init()
        }
      })
      },

    screenWidth(val){
        // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
        if(!this.timer){
            // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
            this.screenWidth = val
            this.timer = true
            let that = this
            setTimeout(function(){
                // 打印screenWidth变化的值
                console.log(that.screenWidth)
                that.timer = false
                // that.chart.clear()
                that.chart.resize()
            },400)
        }
    }
  }
  
}
</script>
<style scoped>

</style>