// 指标选择框
<template>
  <div>
    <span id='text'>指标名称:</span> 
            <el-select v-model="indexValue" placeholder="请选择" :style='widthSet' @change="submitIndex"  filterable>
              <el-option
                v-for="item in indexSet"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
  </div>
  
</template>
<script>
export default { 
  props:['indexSet'],
  data(){
    return {
      indexValue:'客流量',//选择的指标
      indexList:[],//指标列表
      textWidth:''

    }
  },
  components:{

  },
  mounted(){
      this.textWidth=document.getElementById('text').offsetWidth
  },
  methods:{
    submitIndex(val){
      this.$emit('indexGet',val)
    }
  },
  computed:{
    widthSet(){
      return `width:calc(90% - ${this.textWidth}px);`
    }
  },
  watch:{
    indexSet(val){
      this.indexValue=val
    }
  }

}
</script>
