// 商场选择框
<template>
  <div>
    <el-radio-group v-model="dateTime" @change='submitTime'>
      <el-radio-button label="近7日"></el-radio-button>
      <el-radio-button label="近30日"></el-radio-button>
      <el-radio-button label="近1年"></el-radio-button>
      <el-radio-button label="自定义时间"></el-radio-button>
    </el-radio-group>
  </div>
  
</template>
<script>

export default { 
  props:['timeSet'],
  data(){
    return {
      timeValue:'',//选择的商场
      timeList:[],//商场列表
      dateTime:'近7日'
    }
  },
  components:{

  },
  mounted(){
  },
  methods:{
    submitTime(val){
      this.$emit('timeGet',val)
    }
    },
    watch:{
      timeSet(val){
        this.timeValue=val
      }
    }
  }


</script>
